body {
  background-color: black;
  color: white;
}

.icon {
  width: 30px;
  height: 30px;
  cursor: pointer;
}

.small-icon {
  width: 20px;
  height: 20px;
  cursor: pointer;
}

.side-menu {
  background-color: rgba(3, 130, 56);
  padding: 36px 20px;
  height: 100%;
  position: absolute;
  width: 300px;
  left: 0;
  top: 0;
  z-index: 1000;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
}

.side-menu-link {
  display: flex;
  flex-direction: row;
  align-items: center;
  border-radius: 10px;
  font-family: 'Nunito', sans-serif;
  font-size: 20px;
  padding: 10px;
  margin: 10px 0;
}

.side-menu-link:hover {
  transform: scale(1.01);
  background-color: rgba(255, 255, 255, 0.2);
}

a {
  text-decoration: none;
  color: white;
}

.nav-bar {
}

p {
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  font-size: 20px;
}

h1 {
  font-family: 'Nunito', sans-serif;
  font-weight: 600;
  text-transform: uppercase;
  font-size: 26px;
  margin-right: 15px;
  text-shadow: 0 0 8px black;

}

h2 {
  font-family: 'Nunito', sans-serif;
  font-size: 40px;
}

input {
  outline: none;
  border: 0;
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  font-size: 16px;
}

select {
  margin: 5px;
  border: none;
  font-family: 'Nunito', sans-serif;
  padding: 2px 5px;
  user-select: none;
}

select:focus {
    outline-width: 0;
}

button {
  margin: 5px;
  border: none;
  font-family: 'Nunito', sans-serif;
  font-weight: 600;
  padding: 10px;
  cursor: pointer;
  color: white;
  background-color: rgba(3, 130, 56);
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  border-radius: 5px;
  font-size: 16px;
}

button:focus {
    outline-width: 0;
}

.search-box {
  background-color: white;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  width: 260px;
  max-width: 260px;
  color: black;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  position: relative;
}

.page {
  padding: 20px;
  font-family: 'Nunito', sans-serif;
}

.row {
  display: flex;
  flex-direction: row;
}

.centered-row {
  display: flex;
  align-items: center;
  flex-direction: row;
}

.spaced-row {
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
}

.col {
  display: flex;
  flex-direction: column;
}

.centered-col {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
}

.scene-selector {
  border-radius: 10px;
  margin: 20px 0;
  overflow: hidden;
  height: 200px;
  position: relative;
  box-shadow: inset 0 40px 100px rgba(0, 0, 0, 0.5);
}

.full-screen-scene {
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  z-index: 1000;
}

.audio-selector {
  background-color: white;
  padding: 5px 10px;
  border-radius: 5px;
  cursor: pointer;
  width: 200px;
  max-width: 200px;
  color: black;
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  position: relative;
}

.dropdown-menu {
  background-color: white;
  border-radius: 5px;
  cursor: pointer;
  width: 200px;
  color: black;
  font-family: 'Nunito', sans-serif;
  font-weight: 400;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  position: absolute;
  top: 120%;
  left: 0;
  overflow:hidden;
}

.dropdown-selection {
  padding: 10px;
}

.dropdown-selection:hover {
  background-color: #EFEFEF;
}

.play-button {
  height: 40px;
  width: 40px;
  border-radius: 50%;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  display: flex;
  justify-content: center;
  align-items: center;
  color: black;
  background-color: white;
}




.calendar-container {
  border-radius: 10px;
  border: 1px solid rgba(61, 61, 61);
  margin: 20px 0;
  overflow: auto;
  height: 100%;
  position: relative;
  width: 100%;
  min-width: 300px;
  font-family: 'Nunito', sans-serif;
}

.calendar-hour-slot {
  border-top: 1px dashed rgba(61, 61, 61);
  height: 99px;
  position: relative;
}

.calendar-label {
  position: absolute;
  top: 10px;
  left: 10px;
}

.calendar-hour-slot:first-child {
  border: none;
}

.calendar-slot {
  height: 25px;
  width: 100%;
  cursor: pointer;
  position: relative;
}

.break-slot {
  height: 25px;
  position: absolute;
  top: 0;
  right: 5%;
  width: 70%;
  background-color: rgba(3, 130, 56);
  border-radius: 5px;
  box-sizing: border-box;
  padding: 2px 10px;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.2);
  overflow: visible;
  cursor: pointer;
}

.calendar-scheduler {
  position: absolute;
  bottom: -150px;
  left: 8%;
  height: 280px;
  width: 360px;
  box-shadow: 0 0 10px rgba(0, 0, 0);
  border-radius: 10px;
  padding: 10px 20px;
  background-color: rgba(61, 61, 61);
  border: 1px solid rgba(204, 204, 204);
  z-index: 1000;
}


.calendar-overlay {
  position: absolute;
  background-color: rgba(255, 255, 255, 0.2);
  right: 5%;
  width: 70%;
  border-radius:10px;
  box-sizing: border-box; 
  padding: 10px;
  cursor: pointer;
  z-index: -1;
}

.name-circle {
  width: 45px;
  height: 45px;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(204, 204, 204);
  cursor: pointer;
  color: rgba(204, 204, 204);
  font-size: 24px;
  font-weight: 600;
  background-color: rgba(61, 61, 61);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px 10px;
  box-sizing: border-box;
}


.name-circle:hover {
  transform: scale(1.05);
}

.big-name-circle {
  width: 65px;
  height: 65px;
  border-radius: 50%;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  border: 1px solid rgba(204, 204, 204);
  cursor: pointer;
  color: rgba(204, 204, 204);
  font-size: 28px;
  font-weight: 600;
  background-color: rgba(61, 61, 61);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px 10px;
  box-sizing: border-box;
}

.now-line {
  height: 2px;
  border-top: 2px dashed rgba(3, 130, 56);
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
}

.friend-card {
  font-family: 'Nunito', sans-serif;
  margin: 10px 0;
  display: flex;
}

.calendar-popup {
  position: absolute;
  left: 0;
  top: 1;
  background-color: black;
  box-shadow: 0 0 10px rgba(0, 0, 0);
  paddding: 10px;
  z-index: 2000;
  height: 400px;
}

.profile-name-circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  font-family: 'Nunito', sans-serif;
  box-shadow: 0 0 5px rgba(0, 0, 0, 0.5);
  cursor: pointer;
  color: white;
  font-size: 36px;
  font-weight: 600;
  background-color: rgba(3, 130, 56);
  display: flex;
  align-items: center;
  justify-content: center;
  margin: 5px 10px;
  box-sizing: border-box;
}

.next-break {
  background-color: rgba(255, 255, 255, 0.2);
  color: white;
  font-family: 'Nunito', sans-serif;
  box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
  border-radius: 10px;
  padding: 10px;
  margin: 10px 0;
  cursor: pointer;
}

.next-break:hover {
  transform: scale(1.01)
}

.selected {
  border: none;
  background-color: rgba(3, 130, 56);
  color: white;
}

.light-break {
  background-color: white;
  color: black;
}
